import React, { useState } from "react";

import { Paper, InputBase } from "@mui/material";
import { styled } from "@mui/material/styles";
import SearchIcon from "@mui/icons-material/Search";

const SelfPaper = styled(Paper)(({ theme }) => ({
  border: "1px solid #E2E2E2",
  p: "0 0 0 4px",
  display: "inline-flex",
  alignItems: "center",
  width: 400,
  height: 36,
  boxShadow: "none",
  borderRadius: "5px",
}));

const SearchPane = styled("div")(({ theme }) => ({
  display: "flex",
  justifyContent: "center",
  alignItems: "center",
  padding: "0 3px",
  background: "#075FB8",
  height: "100%",
  width: 36,
  color: "#fff",
  borderRadius: "0 5px 5px 0",
  cursor: "pointer",
}));

export default function QueryPane(props) {
  const [value, setValue] = useState("");

  const handleClick = () => {
    const { search } = props;
    search && search(value);
  };

  const changeVal = (e) => {
    setValue(e.target.value);
  };

  const keyEvent = (e) => {
    if (e.keyCode !== 13) {
      return;
    }
    handleClick();
  };

  return (
    <SelfPaper>
      <InputBase
        sx={{ ml: 1, flex: 1 }}
        placeholder="搜索"
        value={value}
        onChange={changeVal}
        onKeyUp={keyEvent}
      />
      <SearchPane onClick={handleClick}>
        <SearchIcon />
      </SearchPane>
    </SelfPaper>
  );
}
