import React from "react";

import { TablePagination } from "@mui/material";

interface STableProp {
  total?: number;
  pagenation?: {
    pageNum?: number;
    pageSize?: number;
    pageSizeOptions?: Array<number>;
  };
  onPageChange?: (pageNum: number) => void;
  onRowsPerPageChange?: (pageSize: number) => void;
}

const STable: React.FC<STableProp> = ({
  total,
  pagenation,
  onPageChange,
  onRowsPerPageChange,
}) => {
  const { pageNum, pageSize, pageSizeOptions } = pagenation;

  const changePage = (event: unknown, newPage: number) => {
    onPageChange && onPageChange(newPage + 1);
  };

  const changeRowsPerPage = (event: React.ChangeEvent<HTMLInputElement>) => {
    onRowsPerPageChange &&
      onRowsPerPageChange(parseInt(event.target.value, 10));
  };

  return (
    <TablePagination
      rowsPerPageOptions={pageSizeOptions || []}
      component="div"
      count={total}
      rowsPerPage={pageSize}
      page={pageNum > 0 ? pageNum - 1 : 0}
      onPageChange={changePage}
      onRowsPerPageChange={changeRowsPerPage}
      labelRowsPerPage="每页条数："
      labelDisplayedRows={({ from, to }) => {
        return `${from}–${to} 共 ${Math.ceil(total / pageSize)}页, ${total}条`;
      }}
    />
  );
};

export default STable;
