import React, { useEffect, useState } from "react";

import { styled } from "@mui/material/styles";
import { Grid, Box } from "@mui/material";

import STable from "~/components/STable";
import QueryPane from "~/components/QueryPane";

import { getAction } from "~/apis/manage";

const url = {
  list: "editor/opus/list/private",
  add: "",
  edit: "",
  delete: "",
  project: "",
};

const Nav = styled("div")(({ theme }) => ({
  display: "flex",
  height: "50px",
  justifyContent: "space-between",
  alignItems: "center",
  fontSize: "15px",
  padding: "15px 0",
}));

export default function Deploy(props) {
  const [dataSource, setDataSource] = useState([]);
  const [ipagenation, setIpagenation] = useState({
    pageNum: 1,
    pageSize: 10,
    pageSizeOptions: [],
  });
  const [total, setTotal] = useState(0);
  let columns = [
    {
      key: "",
      title: "序号",
      align: "center",
      render: (text, record, index) => index + 1,
    },
    {
      key: "name",
      title: "名称",
      align: "center",
    },
    {
      key: "created_at",
      title: "创建时间",
      align: "center",
      render: (text, record, index) => text || "-",
    },
    {
      key: "updated_at",
      title: "审核时间",
      align: "center",
      render: (text, record, index) => text || "-",
    },
    {
      key: "audit_status",
      title: "状态",
      align: "center",
      render: (text, record, index) => {
        let name = "",
          color = "";
        if (text == "0") {
          name = "待审核";
          color = "#333";
        } else if (text == "1") {
          name = "已通过";
          color = "#308A43";
        } else if (text == "2") {
          name = "被驳回";
          color = "#FF3B3B";
        }
        return <span style={{ color, fontSize: "14px" }}>{name}</span>;
      },
    },
    {
      key: "refuse",
      title: "审批意见",
      align: "center",
      render: (text, record, index) => text || "-",
    },
  ];

  /* --start */
  // 获取列表
  const getDataList = (params?: {}) => {
    let formData = {
      page: ipagenation.pageNum,
      limit: ipagenation.pageSize,
      ...params,
    };
    getAction(url.list, formData)
      .then((res) => {
        if (res["code"] === 0) {
          let { list, total_count } = res.data;
          if (list && list.length > 0) {
            setDataSource(list);
            setTotal(total_count);
          } else {
            setDataSource([]);
            setTotal(0);
          }
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };

  useEffect(() => {
    getDataList();
  }, []);

  const handleChangePage = (page) => {
    setIpagenation({
      pageNum: page,
      pageSize: ipagenation.pageSize,
      pageSizeOptions: ipagenation.pageSizeOptions,
    });
    getDataList({ page });
  };

  const handleChangeRowsPerPage = (rowsPerPage) => {
    setIpagenation({
      pageNum: 1,
      pageSize: rowsPerPage,
      pageSizeOptions: ipagenation.pageSizeOptions,
    });
    getDataList({ limit: rowsPerPage });
  };

  const search = (val) => {
    getDataList({ name: val });
  };

  return (
    <Box sx={{ flexGrow: 1, height: "100%" }}>
      <Grid container spacing={0} sx={{ height: "100%" }}>
        <Grid item xs={12} sx={{ padding: "0 15px" }}>
          <Nav>
            <span style={{ fontWeight: "bold" }}>我的作品</span>
            <QueryPane search={search}></QueryPane>
          </Nav>
        </Grid>
        <Grid
          item
          xs={12}
          sx={{
            background: "rgb(240, 242, 245)",
            padding: "15px",
            height: "calc(100% - 50px)",
          }}
        >
          <STable
            columns={columns}
            dataSource={dataSource}
            rowKey="id"
            ipagenation={ipagenation}
            handleChangePage={handleChangePage}
            handleChangeRowsPerPage={handleChangeRowsPerPage}
            tableStyle={{ overflowY: "auto" }}
            total={total}
          ></STable>
        </Grid>
      </Grid>
    </Box>
  );
}
