import React, { useState } from "react";

import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Checkbox,
  TableSortLabel,
  Box,
} from "@mui/material";
import { visuallyHidden } from "@mui/utils";
import { styled } from "@mui/material/styles";

import { getUUID } from "~/utils/common";
import SPagination from "~/components/SPagination";
interface STableProp {
  // props: {
  columns?: Array<any>;
  dataSource?: Array<any>;
  rowKey?: string;
  ipagenation?: {
    pageNum: number;
    pageSize: number;
    pageSizeOptions: Array<number>;
  };
  handleChangePage?: (pageNum: number) => void;
  handleChangeRowsPerPage?: (pageSize: number) => void;
  selectedRowKeys?: Array<number | string>;
  selectionRows?: Array<object>;
  selectedChange?: Function;
  changeSort?: Function;
  tableStyle?: object;
  total?: number;
  // };
  // state: {
  //   sortType?: object;
  // };
}

const NoData = styled("div")(({ theme }) => ({
  textAlign: "center",
  fontSize: "14px",
  padding: "20px 0",
  "& img": {
    width: 300,
    height: 300,
  },
  "& p": {
    margin: 0,
  },
}));

const SelfTableCell = styled(TableCell)(({ theme }) => ({
  padding: "15px",
}));

const STable: React.FC<STableProp> = ({
  columns,
  dataSource,
  rowKey,
  ipagenation,
  handleChangePage,
  handleChangeRowsPerPage,
  selectedRowKeys,
  selectionRows,
  selectedChange,
  changeSort,
  tableStyle,
  total,
}) => {
  const [sortType, setSortType] = useState({});

  const createColumns = (list) => {
    let startNum = ipagenation.pageNum * ipagenation.pageSize,
      endNum =
        ipagenation.pageNum * ipagenation.pageSize + ipagenation.pageSize;
    let length = dataSource.slice(startNum, endNum).length;
    return (
      <TableRow>
        {selectedRowKeys ? (
          <SelfTableCell key={getUUID()} padding="checkbox">
            <Checkbox
              color="primary"
              checked={
                selectedRowKeys.length > 0 && selectedRowKeys.length === length
              }
              onChange={(event) => onSelectAllClick(event, null, null)}
              indeterminate={
                selectedRowKeys.length > 0 && selectedRowKeys.length < length
              }
              // inputProps={{
              //   "aria-label": "select all desserts",
              // }}
            />
          </SelfTableCell>
        ) : null}
        {list.map((item) =>
          item.sort ? (
            <SelfTableCell
              key={item.key}
              align={item.align ? item.align : "left"}
              sortDirection={sortType ? "desc" : "asc"}
              onClick={() => changeSortMethod(sortType, item.key)}
            >
              <TableSortLabel direction={sortType[item.key] ? "desc" : "asc"}>
                {item.title}
              </TableSortLabel>
              <Box component="span" sx={visuallyHidden}>
                {/* {order === "desc" ? "sorted descending" : "sorted ascending"} */}
                {"sorted descending"}
              </Box>
            </SelfTableCell>
          ) : (
            <SelfTableCell
              key={item.key}
              align={item.align ? item.align : "left"}
            >
              {item.title}
            </SelfTableCell>
          )
        )}
      </TableRow>
    );
  };

  const changeSortMethod = (type, column) => {
    let newType = !type[column];
    let newSortType = JSON.parse(JSON.stringify(sortType));
    newSortType[column] = newType;
    // debugger;
    setSortType(newSortType);
    changeSort && changeSort(newType, column);
  };

  const onSelectAllClick = (event, key, item) => {
    let checked = event.target.checked;
    let newSelectedRowKeys = [],
      newSelectionRows = [];
    if (!key && !item) {
      newSelectedRowKeys = checked ? getAllSelected().rowKeys : [];
      newSelectionRows = checked ? getAllSelected().rows : [];
    } else {
      if (checked) {
        newSelectedRowKeys = [...selectedRowKeys, key];
        newSelectionRows = [...selectionRows, item];
      } else {
        newSelectedRowKeys = [...selectedRowKeys];
        newSelectionRows = [...selectionRows];
        let index = newSelectedRowKeys.indexOf(key);
        newSelectedRowKeys.splice(index, 1);
        newSelectionRows.splice(index, 1);
      }
    }
    selectedChange && selectedChange(selectedRowKeys, selectionRows);
  };

  const getAllSelected = () => {
    let startNum = ipagenation.pageNum * ipagenation.pageSize,
      endNum =
        ipagenation.pageNum * ipagenation.pageSize + ipagenation.pageSize;
    let rows = dataSource.slice(startNum, endNum);
    let rowKeys = dataSource.slice(startNum, endNum).map((item) => {
      return item[rowKey];
    });
    return {
      rows,
      rowKeys,
    };
  };

  const createDataSource = (data, index) => {
    let itemArr = [];
    // for (let key in data) {
    let dataKeys = Object.keys(data);
    for (let colItem of columns) {
      if (colItem.render) {
        let text = "";
        let res = dataKeys.find((key) => key === colItem.key);
        if (res) {
          text = data[colItem.key];
        }
        itemArr.push(
          <SelfTableCell
            key={getUUID()}
            align={colItem.align ? colItem.align : "left"}
          >
            {colItem.render(text, data, index)}
          </SelfTableCell>
        );
      } else {
        let res = dataKeys.find((key) => key === colItem.key);
        if (res) {
          itemArr.push(
            <SelfTableCell
              key={colItem.key}
              align={colItem.align ? colItem.align : "left"}
            >
              {data[colItem.key]}
            </SelfTableCell>
          );
        } else {
          itemArr.push(
            <SelfTableCell
              key={getUUID()}
              align={colItem.align ? colItem.align : "left"}
            ></SelfTableCell>
          );
        }
      }
    }
    return itemArr;
  };

  const changePage = (event: unknown, newPage: number) => {
    handleChangePage && handleChangePage(newPage + 1);
  };

  const changeRowsPerPage = (event: React.ChangeEvent<HTMLInputElement>) => {
    handleChangeRowsPerPage &&
      handleChangeRowsPerPage(parseInt(event.target.value, 10));
  };

  const getStatus = (id, arr) => {
    let res = arr.find((item) => item === id);
    if (res) {
      return true;
    } else {
      return false;
    }
  };

  const renderTable = () => {
    const tableRowKey = rowKey || "id";
    return (
      <Paper sx={{ width: "100%", mb: 2, height: "100%", overflow: "auto" }}>
        <TableContainer
          sx={{
            width: "100%",
            overflow: "auto",
            height: "100%",
            minHeight: 650,
            ...(tableStyle || {}),
          }}
        >
          <Table aria-label="simple table" sx={{ overflowY: "auto" }}>
            <TableHead>{columns ? createColumns(columns) : null}</TableHead>
            <TableBody>
              {dataSource &&
                dataSource.map((row, index) => (
                  <TableRow
                    key={row[tableRowKey] || getUUID()}
                    sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
                  >
                    {selectedRowKeys ? (
                      <SelfTableCell key={getUUID()} padding="checkbox">
                        <Checkbox
                          color="primary"
                          checked={getStatus(row.id, selectedRowKeys)}
                          onChange={(e) => onSelectAllClick(e, row.id, row)}
                        />
                      </SelfTableCell>
                    ) : null}
                    {createDataSource(row, index)}
                  </TableRow>
                ))}
            </TableBody>
          </Table>
          {ipagenation && total > 0 ? (
            <SPagination
              pagenation={ipagenation}
              total={total}
              onPageChange={handleChangePage}
              onRowsPerPageChange={handleChangeRowsPerPage}
            />
          ) : null}
          {!dataSource || dataSource.length === 0 ? (
            <NoData>
              <img src="/static/images/nodata.svg" />
              <p>暂无数据</p>
            </NoData>
          ) : null}
        </TableContainer>
      </Paper>
    );
  };

  return renderTable();
};

export default STable;
